const canvas = document.querySelector("canvas");
const c = canvas.getContext("2d");

canvas.width = innerWidth;
canvas.height = innerHeight;

const mouse = {
  x: innerWidth / 2,
  y: innerHeight / 2
};

const colors = ["#2185C5", "#7ECEFD", "#FFF6E5", "#FF7F66"];

const gravity = 0.5;
const friction = 1;

// Event Listeners
addEventListener("mousemove", event => {
  mouse.x = event.clientX;
  mouse.y = event.clientY;
});

addEventListener("resize", () => {
  canvas.width = innerWidth;
  canvas.height = innerHeight;

  init();
});

// Objects
function Ball(x, y, dy, dx, radius, color) {
  this.x = x;
  this.y = y;
  this.dy = dy;
  this.dx = dx;
  this.radius = radius;
  this.color = color;
}

Ball.prototype.draw = function() {
  c.beginPath();
  c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false);
  c.fillStyle = this.color;
  c.fill();
  c.closePath();
};

Ball.prototype.update = function() {
  if (this.y + this.radius > canvas.height) {
    this.dy = -this.dy * friction;
  } else {
    this.dy += gravity;
  }

  this.y += this.dy;
  this.x += this.dx;

  if (this.x + this.radius > canvas.width || this.x - this.radius < 0) {
    this.dx = -this.dx;
  }

  this.draw();
};

// Implementation
let objects;

let ball;
let ballArr = [];

function init() {
  ball = new Ball(canvas.width / 2, canvas.height / 5, 2, 8, 30, "red");

  for (let i = 0; i < 120; i++) {
    let x = randomIntFromRange(0 + ball.radius, canvas.width - ball.radius),
      y = randomIntFromRange(0, canvas.height - ball.radius),
      color = randomColor(colors);

    ballArr.push(new Ball(x, y, 2, 8, 15, color));
  }
}

// Animation Loop
function animate() {
  requestAnimationFrame(animate);
  c.clearRect(0, 0, canvas.width, canvas.height);
  ball.update();

  ballArr.forEach(item => {
    item.update();
  });

  // c.fillText("HTML CANVAS BOILERPLATE", mouse.x, mouse.y);
  // objects.forEach(object => {
  //  object.update()
  // })
}

init();
animate();

function randomIntFromRange(min, max) {
  return Math.floor(Math.random() * (max - min + 1) + min);
}

function randomColor(colors) {
  return colors[Math.floor(Math.random() * colors.length)];
}

function distance(x1, y1, x2, y2) {
  const xDist = x2 - x1;
  const yDist = y2 - y1;

  return Math.sqrt(Math.pow(xDist, 2) + Math.pow(yDist, 2));
}
